import { createRouter, createWebHistory } from 'vue-router';
import MainIndex from './main.vue'
import BasePractice from './sample/basePractice.vue'
import SampleBase from './sample/sampleBase.vue'
import NotFound from './views/exception/NotFound.vue'

console.log('router.js');

const routes = [
    { path: "/", redirect:"/main" },
    { path: "/main", component: MainIndex, },
    { path: "/base", component: BasePractice, },
    { path: "/sample", component: SampleBase, },
    { path: "/cloth", component:() => import('@/views/category/cloth.vue'), },
    { path: "/login", component:() => import('./views/loginForm.vue'), },
    { path: "/boardList", component:() => import('@/views/board/boardList.vue'), },
    { path: "/boardCreate", component:() => import('@/views/board/boardCreate.vue'), 
        beforeEnter: (to, from) => {
            console.log(to);
            console.log(from);
            return checkLogin();
        },
    },
    { path: "/boardEdit/:pageKey", component:() => import('@/views/board/boardEdit.vue'), 
        beforeEnter: () => {
            return checkLogin();
        },
    },
    { path: "/boardDetail/:pageKey", component:() => import('@/views/board/boardDetail.vue'), 
        beforeEnter: () => {
            return checkLogin();
        },
    },
    { path: "/scheduleList", component:() => import('@/views/schedule/scheduleList.vue'), 
        beforeEnter: () => {
            return checkLogin();
        },
    },
    { path: "/scheduleOneDay/:yyyymmdd", component:() => import('@/views/schedule/scheduleEdit.vue'), 
        beforeEnter: () => {
            return checkLogin();
        },
    },
    { path: "/notFound", component: NotFound },
    { path: "/:pathMatch(.*)*", redirect: "/notFound" },
];

const checkLogin = () =>{
    if(!sessionStorage.getItem('AccessKeyJwt')) {
        alert('로그인이 필요');
        // 라우트 진입 거부
        return false;
    } else {
        return true;
    }
}

const menuJs = createRouter({
    history: createWebHistory(),
    routes,
})
//뒤로가기 시 axios를 통한 조회가 이루어 져도 router에 쌓이는 현상 발생. 같은 url이면 히스토리에 쌓지 않기
menuJs.beforeEach((to, from) => {
//    console.log('beforEach');    
//    console.log(to);
//    console.log(from);
    if(to.fullPath == from.fullPath) {
        //router 진입거부.(push 하지 않음)
        return false;
    }
})


export default menuJs