import { createApp } from 'vue'
import startIndex from './index.vue'
import RouterJs from './router.js';
import store from "./components/common/store";

// 다른 컴포넌트에서 import 없이 this.$axios로 사용가능. 단 script setup이 아니라 일반 script 에서만 가능. setup은 미리 수행하기에 해당값들이 없음
//import axios from 'axios'
//Vue.prototype.$axios = axios  이거 보다는 use(axios) 가 나을 듯
import axios from "axios";

//package.json에서 eslintConfig부분에 "vue/setup-compiler-macros": true 추가해주면 import defineProps 안해줘도 된다

const getMenu = () => {
    return axios.post("/menuList.do",{"sysGroup":"CAR"})
    .then((res) => {
      console.log('조회결과',res.data.RESULT_LIST);
      return res.data.RESULT_LIST
    })
    .catch((res) => {
      console.error("에러", res);
      return 'ERROR'
    });
      
  } 

const dbMenu = await getMenu();
console.log('main.js');
/*
const app = createApp(index)
app.use(router) 
app.mount('#app')
*/
createApp(startIndex).provide('$dbMenu',dbMenu).use(store).use(RouterJs).mount('#app')
